* {
    margin: 0;
}

html {
    scroll-behavior: smooth;
    width: 100%;
    height: 100%;
}

/* HOMEPAGE */

/* INTRO */

.intro {
    margin: 10%;
}

/* ROMEINSE RIJK- KAART 1 */

/* leaflet */

#mapid {
    /* height: 500px;
    width: 670px; */
    height: 50vh;
    width: 40vw;
    margin-left: 5%;
    margin-bottom: 5%;
}

.info {
    padding: 6px 8px;
    font: 14px 'Montserrat', sans-serif;
    background: white;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
}

.info h4 {
    margin: 0 0 5px;
    color: #000000;
}

/* ROMEINSE RIJK - KAART2 */

/* esri */

.embed-container {
    position: relative;
    padding-bottom: 80%;
    height: 0;
    max-width: 100%;
}

.embed-container iframe, .embed-container object, .embed-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 40%;
    height: 40%;
    margin-left: 5%;
    margin-bottom: 5%;
}

small {
    position: absolute;
    z-index: 40;
    bottom: 0;
    margin-bottom: -15px;
}

